import { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { TaskContext } from "../AppStates";
import Select from 'react-select';


export default function Profile() {
    const { user, routas, setTasks, setUser } = useContext(TaskContext)
    console.log(user)
    const [pavadinimas, setPavadinimas] = useState(user.company_name)
    const [apie, setApie] = useState(user.about_company)
    const [url, setUrl] = useState(user.company_url)
    const [sk, setSK] = useState('')
    const [kur, setKur] = useState(0)
    const [pas, setPas] = useState('')
    const [pas2, setPas2] = useState('')
    const [pas3, setPas3] = useState('')
    const { t, i18n } = useTranslation()
    const [currentLng, setCurrentLng] = useState(i18n.language)
    // const [sritysD, setsritysD] = useState('')

    var sritys = []

    t('sritys').forEach((e, i) => {
        sritys.push({ value: i, label: e })
    })
    sritys = sritys.sort((a, b) => a.label > b.label ? 1 : -1)


    const gather = (item, vertimas) => {
        if (item == undefined) { return '' }
        let splited = item.split(',')
        let arr = []
        splited.forEach(element => {
            if (!isNaN(element) && element) {
                arr.push({
                    value: element,
                    label: vertimas[element]
                })
            }

        });
        return arr
    }

    const [sritysO, setsritysO] = useState('')
    useEffect(() => {
        setsritysO(gather(user.darbo_sritis, t('sritis')))
    }, [])

    const istrint = async () => {
        let deleteRoute = routas + 'users/' + user._id;

        let trinti = {
            method: 'delete',
            headers: { 'Content-Type': 'application/json' },
        }
        try {
            const response = await fetch(deleteRoute, trinti)
            const json = await response.json().then(data => {
                setTasks('login')
            })
        } catch (error) {
            console.error(error);
        }

    }
    const apdoroti = (item) => {
        if (item == null) { return }
        if (item != null || item != '')
            try {
                let data = ''
                item.forEach((e, i) => {
                    if (i != item.length - 1) {
                        data += e.value + ','
                    } else { data += e.value }
                })
                return data
            }
            catch (e) { console.log(e) }
    }
    const updateInfo = async () => {
        let requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body:
                JSON.stringify({
                    darbo_sritis: apdoroti(sritysO),
                    company_name: pavadinimas,
                    about_company: apie,
                    company_url: url,
                    number_of_emploees: sk,

                })
        };
        let update_employe = routas + 'users/update/' + user.access_token;
        try {
            const response = await fetch(update_employe, requestOptions)
            const json = await response.json().then(data => {
                setUser(data)
            })
        } catch (error) {
            console.error(error);
        }
    };
    const updatePass = async () => {
        try {
            let pswRoute = routas + `users/${user._id}/password`;

            let updatePSW = {
                method: 'PATCH',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    password: pas,
                    updated_password: pas2
                })
            }
            if (pas2 == pas3) {
                if (pas == pas2 && pas3 == pas) {
                    alert(t('psw_visi_tokie_pat'))
                    return
                }
                const response = await fetch(pswRoute, updatePSW)
                const json = await response.json().then(data => {
                    console.log(data)
                    if (data.statusCode == 400) {
                        alert(t('psw_blogas'))
                    }
                    else {
                        alert(t('psw_pakeistas'))
                    }
                }

                )
            }
            else {
                alert(t('psw_skirtingi'))
            }
        }
        catch (e) {

        }
    }
    return (
        <main style={{ padding: "1rem 0" }}>
            <h2>{t('Profilis')}</h2>
            <div className="mainSkelbimai">

                <div className="leftSkelbimai">


                    <div className="adContainer1">
                        <div className="adsList1"
                            onClick={() => {
                                setKur(0)
                            }}>
                            <a>{t('apie_imone')}</a>
                        </div>
                        <div className="adsList1"
                            onClick={() => {
                                setKur(1)
                            }}>
                            <a>{t('slaptazodzio_keitimas')}</a>
                        </div>
                        <div className="adsList1" onClick={() => { window.open('https://scobo.lt/' + currentLng + '/privacy-policy/', '_blank'); }}>
                            <a>{t('duomenu_politika')}</a>
                        </div>
                        <div style={{ marginBottom: 50 }}>
                            <button className="adsList1" onClick={() => {

                                // setVisible(!visible)
                                // setDialog('istrinti')
                                istrint()

                            }}> {t('istrinti')}
                                {/* <Icon name="close" size={20} /> */}
                            </button>
                        </div>
                    </div>
                </div>
                <div className="rightSkelbimai">
                    <div className=" centerContainer">
                        {kur == 0 ? (
                            <div className="rightContainer1">
                                <h2> {t('apie_imone')}</h2>
                                <input
                                    required
                                    className="input"
                                    placeholder={t('imones_pavadinimas')}
                                    value={pavadinimas}
                                    onChange={(e) => { setPavadinimas(e.target.value) }}
                                />
                                <textarea
                                    type="textarea"
                                    className="textarea"
                                    placeholder={t('apie_imone')}
                                    value={apie}
                                    onChange={(e) => { setApie(e.target.value) }}
                                />
                                <input
                                    className="input"
                                    placeholder={t('svetaine')}
                                    value={url}
                                    onChange={(e) => { setUrl(e.target.value) }}
                                />
                                <input
                                    className="input"
                                    type={'number'}
                                    placeholder={t('darbuotoju_sk')}
                                    value={sk}
                                    onChange={(e) => { setSK(e.target.value) }}
                                />
                                <Select
                                    className="select"
                                    placeholder={t('veiklos_sritys')}
                                    isMulti={true}
                                    defaultValue={sritysO}
                                    onChange={setsritysO}
                                    options={sritys}
                                />
                                <button
                                    onClick={() => {

                                    }}
                                    className="mainBtn ">{t('issaugoti')}</button>
                            </div>
                        ) : (
                            <div className="rightContainer1">
                                <h2> {t('slaptazodzio_keitimas')}</h2>
                                <input
                                    required
                                    type={'password'}
                                    className="input"
                                    placeholder={t('sena_slaptazodi')}
                                    value={pas}
                                    onChange={(e) => { setPas(e.target.value) }}
                                />
                                <input
                                    required
                                    type={'password'}
                                    className="input"
                                    placeholder={t('nauja_slaptazodi')}
                                    value={pas2}
                                    onChange={(e) => { setPas2(e.target.value) }}
                                />
                                <input
                                    required
                                    type={'password'}
                                    className="input"
                                    placeholder={t('nauja_pakartoti_slaptazodi')}
                                    value={pas3}
                                    onChange={(e) => { setPas3(e.target.value) }}
                                />

                                <button
                                    onClick={() => {
                                        updatePass()
                                    }}
                                    className="mainBtn ">{t('issaugoti')}</button>
                            </div>
                        )}

                    </div>


                </div>

            </div>
        </main>
    );
}