import { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { TaskContext } from "../AppStates";
import Modal from 'react-modal';


export default function Kandidatai() {
  const { user, routas } = useContext(TaskContext)
  const [data, setData] = useState({})
  const { t } = useTranslation()
  var cardDomain = routas + 'worker-listing/darbdaviui/' + user._id
  const requestOptions = {
    method: 'GET',
    headers: { 'Content-Type': 'application/json' },
  };
  const getCard = async () => {
    fetch(cardDomain, requestOptions)
      .then(async response => {
        await response.json().then(data => {
          if (data.statusCode != 500 && data != []) {
            setData(data)
            console.log(data)
          }
        })
      })
      .catch(error => {
        console.error('There was an error!', error);
        return false
      });
  }
  useEffect(() => {
    getCard()
  }, [])
  const perKalbleli = (item, kas) => {
    if (item && item != 'undefined' && item != '' && item != undefined) {
      let visos = item.split(',')
      let html = ''
      visos.forEach((element, index) => {
        if (element == 'nuotoliniu') { html += t('nuotoliniu') }
        else if (element == 'nuotoliniu') { html += t('visi_miestai') }
        else {
          if (element != '' && kas[element] != undefined)
            if (index != visos.length - 1 && visos[index + 1] != '') {
              html += kas[element] + ', '
            }
            else {
              html += kas[element]
            }
        }
      });
      return html
    }
    else {
      return ''
    }
  }

  const [modalIsOpen, setModalIsOpen] = useState(false)
  // const handleClose = () => setModalIsOpen(false);
  const handleShow = () => setModalIsOpen(true);

  const [info, setInfo] = useState({})

  const Langelis = (checker,pavadinimas, aprasas) => {
    if (!checker || checker == 'undefined' || checker == '' || checker == undefined) { return }
    if (aprasas != undefined)
      aprasas = aprasas.replace('undefined', '')

    return (
      <div>
        <div >
          <div >
            <h4 className="inModalInfo">{pavadinimas}</h4>
            <h5 className="inModalInfo1">{aprasas}</h5>
          </div>
        </div>
      </div>
    )
  }

  const kalbos = (item) => {
    if (item && item != 'undefined' && item != '' && item != undefined) {
      let visos = item.split(',')
      let html = ''
      visos.forEach((element, index) => {
        if (element != '') {
          if (index != visos.length - 1 && visos[index + 1] != '') {
            html += t('kalbos')[element.split(':')[0]] + ': ' + t('kalbos_lygiai')[element.split(':')[1]]+"; ";
          }
          else {
            html += t('kalbos')[element.split(':')[0]] + ': ' + t('kalbos_lygiai')[element.split(':')[1]]
          }
        }
      });
      return html
    }
    else {
      return ''
    }
  }

  const issaugoti = async (mano, darbuotojo) => {
    const get_listing = routas + 'users/save/' + mano + '/' + darbuotojo;
    const get_skelbimai = {
      method: 'PATCH',
      headers: { 'Content-Type': 'application/json' },
    }

    try {
      const response = await fetch(get_listing, get_skelbimai)
      getCard()
    } catch (error) {
      console.error(error);
    }
  };



  const kortele = (item, i) => {
    return (
      <div key={i} className="kortele" onClick={() => { handleShow(); setInfo(item) }} >
        <div className="close"onClick={(e) => {
            e.stopPropagation()
          }}>✖</div>
        <div className="cardInfo">
          <div className="candidateInfo" ><h4>{item.vardas} {item.pavarde}</h4></div>
          <div className="candidateInfo1"><h6>{perKalbleli(item.darbo_sritis, t('darbo_sritis'))}</h6></div>
        </div>
        <div className="smallBtnContainer">
        <div className="smallBtn1">
          <button className="smallBtn"onClick={(e) => {
            e.stopPropagation()
          }} >{t('poke')}</button></div>
          <div className="smallBtn1">
          <button className="smallBtn" onClick={(e) => {
            e.stopPropagation()
            issaugoti(user._id, item._id)
          }}>{t('issaugoti')}</button></div>
        </div>
      </div>
    )
  }
  return (
    <main style={{ padding: "1rem 0" }}>
      {data.length > 0 ? (
        <div>
          <h2>{t('siulomi_darbuotojai')}</h2>

          <div className="korteles">
            {data.map((e, i) => (
              kortele(e, i)
            ))}
          </div>
        </div>
      ) : (
        <h2>{t('skelbimu_nera')}</h2>
      )}

      <Modal
        isOpen={modalIsOpen}
        ariaHideApp={false}
        className="modal1"
      >
        <div
          className="centerContainer1 column">

          <div className="closeBtn">
            <button onClick={() => {
              setModalIsOpen(false)
              // let last = document.querySelectorAll('[aria-label="Remove ' + currentKalba.label + '"]')
              // last[0].click()
            }}>✖</button>
          </div>
          <h3>{info.vardas} {info.pavarde}</h3>
          {console.log(info)}
          <div className="modalInfo" >
            <div
              className="flex column radioContainer1"
            >
              {Langelis(info.studiju_sritis,t('darbuotojo_options_top')[0], t('issilavinimas')[info.issilavinimas] + ' | ' + perKalbleli(info.studiju_sritis, t('sritys')))}
              {Langelis(info.patirtis,t('darbuotojo_options_top')[3], t('patirtis_metais')[info.patirtis] + ' ' + t('metai') + ' ' + ' | ' + perKalbleli(info.darbo_sritis, t('darbo_sritis')))}
              {Langelis(info.kalbos_ir_ju_lygiai,t('darbuotojo_options_top')[6], kalbos(info.kalbos_ir_ju_lygiai))}
              {Langelis(info.etatas,t('poreikis'), t('darbuotojo_options_top')[5] + ':' + perKalbleli(info.etatas, t('etatas')) + info.atlygis)}
              {Langelis(info.sutarties_pobudis,t('darbuotojo_options_top')[4], perKalbleli(info.sutarties_pobudis, t('sutarties_pobudis')))}
              {Langelis(info.aprasymas,t('darbuotojo_options_top')[9], info.aprasymas)}
              {Langelis(info.kontaktinis_el_pastas,t('darbuotojo_options_top')[10], info.kontaktinis_el_pastas+ '/n' + info.kontaktinis_tel_nr) }
              

              

              

            </div>
          </div>
          <button
            onClick={() => {
              setModalIsOpen(false)
            }}
            className="mainBtn modalBtn">{t('issaugoti')}</button>
        </div>

      </Modal>


    </main>



  );
}